









import { Component, Vue } from 'vue-property-decorator';
import FSEvent from './components/FSEvent.vue';
import FSCursor from './components/FSCursor.vue';
import FS2DCanvas from './components/canvas/FS2DCanvas.vue';
import FSMenu from './components/menu/FSMenu.vue';
import EventBus from './EventBus';

import FSObject from './intern/FSObject';
import FSPoint from './intern/FSPoint';

@Component({
  components:{
    FSEvent,
    FS2DCanvas,
    FSCursor,
    FSMenu
  }
})
export default class App extends Vue {

  private t = new FSObject({wh: new FSPoint(10,10)});

  mounted(){
    setTimeout(()=>{
      this.t.wh = new FSPoint(200,200);
    }, 1000);
    EventBus.touch = 'ontouchstart' in window || !!navigator.msMaxTouchPoints;
  }
}
