





import EventBus from '@/EventBus';
import FSPoint from '@/intern/FSPoint';
import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})
export default class FS2DCanvas extends Vue{
  @Prop({ default: () => new FSPoint(window.innerWidth, window.innerHeight) }) wh!: FSPoint;

  $refs!: {
    can: HTMLCanvasElement[];
  }

  private cans!: HTMLCanvasElement[];
  private ctx!: CanvasRenderingContext2D;
  private pts: FSPoint[] = [];
  private stroke: FSPoint[] = [];
  private strokes: FSPoint[][] = [];
  private eraser = false;

  private clear(idx: number){
    const can = this.$refs.can[idx];
    const ctx = can.getContext('2d') as CanvasRenderingContext2D;
    ctx.clearRect(0, 0, can.width, can.height);
  }

  private draw(idx, stroke = this.strokes[this.strokes.length-1]){
    const can = this.$refs.can[idx];
    const ctx = can.getContext('2d') as CanvasRenderingContext2D;

    if(this.eraser){
      ctx.globalCompositeOperation = 'destination-out';
    }else{
      ctx.globalCompositeOperation = 'source-over';
    }

    ctx.beginPath();
    ctx.moveTo(stroke[0].x, stroke[0].y);
    stroke.forEach((el, idx) => {
      ctx.lineTo(el.x, el.y);
    });
    ctx.fill();
    ctx.closePath();
    // ctx.stroke();
  }

  mounted(){
    document.addEventListener('keyup', (e) => {
      if(e.which == 32){
        this.eraser = !this.eraser;
      }
    })

    this.$nextTick(()=>{
      for(const can of this.$refs.can){
        can.width = this.wh.x;
        can.height = this.wh.y;
      }
    });

    EventBus.$on('pDn', (e)=>{
      this.stroke.push(new FSPoint(e.offsetX, e.offsetY));
    });

    EventBus.$on('pMv', (e)=>{
      if(e.pressure && !EventBus.menuHover){
        this.stroke.push(new FSPoint(e.offsetX, e.offsetY));
        this.clear(0);
        this.draw(this.eraser ? 1 : 0, this.stroke);
      }
    });

    EventBus.$on('pUp', ()=>{
      this.strokes.push(this.stroke);
      this.stroke = [];
      this.clear(0);
      this.draw(1);
    });
  }
}
