





import EventBus from '@/EventBus';
import { Component, Vue } from 'vue-property-decorator';

@Component
export default class FSCursor extends Vue{
  $refs!: {
    cur: HTMLCanvasElement;
  }

  private globalID;
  private ctx;

  private draw(){
    const ctx = this.ctx;

    ctx.fillRect(0, 10, 21, 1);
    ctx.fillRect(10, 0, 1, 21);

    ctx.clearRect(8,8,5,5);
    ctx.fillRect(10,10,1,1);
  }

  mounted(){
    const cur = this.$refs.cur;
      cur.width = 21;
      cur.height = 21;
    this.ctx = cur.getContext('2d');

    this.draw();

    this.$nextTick(() => {
      const el = document.getElementById('cursorContainer');

      EventBus.$on('pMv', (e) => {
        if(el){
          el.style.left = e.offsetX-10 + "px";
          el.style.top = e.offsetY-10 + "px";
        }
      });

    });
  }
}
